export default {
	props:{
		resdata:[Array,Object],
		height:{
			type:String,
			default:"350"
		},
		preview:{
			type:Boolean,
			default:false
		}
	},
	computed: {
		getStyle() {
			return `height: ${this.height}rpx;`
		},
		getUrls(){
			return this.resdata.map((v)=> v.src)
		}
	},
	methods:{
		openDetail(id){
			uni.navigateTo({
				url:"/pages/detail/detail?detail="+id
			})
		},
		event(item,index){
			if (this.preview) {
				return uni.previewImage({
					current:index,
					urls:this.getUrls,
					indicator:"default"
				})
			}
		}
	}
}
